<template>
  <div
    id="to-do-list"
    class="change-color"
    :class="colorType"
    @click="$emit('hide-detail')"
  >
    <div v-if="showMask" class="mask" @click="handleCancelAction"></div>
    <h1>ToDoList</h1>
    <div class="block-wrap">
      <div v-if="!loginCheck" class="loading-text">
        Performing login verification...
      </div>
      <div v-else-if="loading" class="loading-text">Loading...</div>
      <div v-else-if="!isLogin">
        <h2 class="block-title">Welcome to ToDoList!</h2>
        <p class="description">
          Please Sign in with Google to use this tool. <br />
          Your information will be saved in the
          <a
            href="https://firebase.google.com/"
            title="Google Firebase Realtime Database"
            target="_blank"
            >Google Firebase Realtime Database</a
          >.
        </p>
        <button class="w-full" @click="$emit('login')">Sign In</button>
      </div>
      <template v-else>
        <ul class="check-list">
          <li
            v-for="(item, index) in list"
            :key="item.id"
            :class="{ isEdit: item.edit }"
          >
            <template v-if="!item.edit">
              <input
                :id="'check' + item.id"
                type="checkbox"
                v-model="item.Status"
                @change="(val) => handleChangeCheck(item)"
              />
              <label
                :for="'check' + item.id"
                title="Click to complete, Drop to move."
                :class="{ active: item.Status }"
                draggable="true"
                :data-index="index"
                @dragstart="(event) => handleDragstart(event)"
                @dragover="(event) => handleDragover(event)"
                @dragleave="(event) => handleDragleave(event)"
                @drop="(event) => handleDrop(event, list)"
              >
                <fa-icon
                  :icon="item.Status ? 'square-check' : 'fa-regular fa-square'"
                ></fa-icon>
                <span>{{ item.Name }}</span>
              </label>
              <button
                class="edit-btn"
                :class="{ active: item.action }"
                title="Action"
                :disabled="item.loading"
                @click="handleOpenAction(item)"
              >
                <fa-icon icon="ellipsis"></fa-icon>
              </button>
              <ul
                class="action-select"
                :class="{
                  active: item.action,
                  top: list.length > 3 && index && index > list.length - 3,
                }"
              >
                <li @click="handleEdit(item)">
                  <fa-icon icon="pen"></fa-icon>
                  Edit
                </li>
                <li v-if="list.length > 1" @click="handleRemove(item)">
                  <fa-icon icon="trash"></fa-icon>
                  Remove
                </li>
              </ul>
            </template>
            <template v-else>
              <div class="front-icon">
                <fa-icon icon="pen"></fa-icon>
                <fa-icon icon="ellipsis" class="small"></fa-icon>
              </div>
              <input
                v-model="item.editName"
                type="text"
                :id="item.id ? 'input' + item.id : 'inputNew'"
                :disabled="item.loading"
                placeholder="Please Enter Name..."
                @keyup.enter="handleSaveChange(item)"
              />
              <button
                class="edit-btn check"
                title="Save Change"
                @click="handleSaveChange(item)"
              >
                <fa-icon icon="check"></fa-icon>
              </button>
              <button
                class="edit-btn"
                title="Cancel Change"
                @click="handleCancelChange(item, index)"
              >
                <fa-icon icon="xmark"></fa-icon>
              </button>
            </template>
          </li>
        </ul>
        <div class="btn-wrap">
          <button
            title="Reset list to default"
            class="white"
            :disabled="loading"
            @click="handleReset"
          >
            <fa-icon icon="trash" class="fix"></fa-icon>
            <span>Reset</span>
          </button>
          <button
            title="Add new item"
            :disabled="loading || disabledAdd"
            @click="handleAdd"
          >
            <fa-icon icon="plus" class="fix"></fa-icon>
            <span>Add</span>
          </button>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import * as database from "firebase/database";
import { getDataBase } from "@/api/config";

export default {
  name: "TodoList",
  props: {
    // 顏色類別
    colorType: {
      type: String,
      default: "default",
    },
    // 使用者資訊
    userInfo: {
      type: Object,
      default: () => ({}),
    },
    // 已登入
    isLogin: {
      type: Boolean,
      default: false,
    },
    // 已通過登入驗證
    loginCheck: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      loading: false,
      dataBase: null,
      list: [],
    };
  },
  computed: {
    // 禁止新增
    disabledAdd() {
      return this.list.find((item) => !item.id);
    },
    // 顯示遮罩
    showMask() {
      return this.list.some((item) => item.action || item.edit);
    },
  },
  watch: {
    // 驗證登入
    isLogin: {
      handler(val) {
        if (val) {
          const uid = this.userInfo.uid;
          const db = getDataBase(`todolist/${uid}`);
          this.dataBase = db;
          this.getData();
        }
      },
      immediate: true,
    },
  },
  mounted() {
    this.setVh();
    // 監聽視窗變化
    window.addEventListener("resize", () => {
      this.setVh();
    });
  },
  methods: {
    /**
     * 取得瀏覽器高度(避免手機Safari跑版)
     */
    setVh() {
      const vh = window.innerHeight * 0.01;
      document.documentElement.style.setProperty("--vh", `${vh}px`);
    },
    /**
     * 取得資料
     */
    async getData() {
      try {
        this.loading = true;
        const data = await database.get(this.dataBase);
        if (data) {
          const obj = data.toJSON();
          if (obj) {
            this.list = Object.entries(obj).map(([key, value]) => {
              return {
                id: key,
                edit: false,
                editName: "",
                loading: false,
                action: false,
                ...value,
              };
            });
          } else {
            this.handleReset();
          }
        }
      } finally {
        this.loading = false;
      }
    },
    /**
     * 全部重置為初始值
     */
    async handleReset() {
      const val = {
        0: {
          Name: "list1",
          Status: false,
        },
        1: {
          Name: "list2",
          Status: false,
        },
      };
      await database.set(this.dataBase, val);
      this.getData();
    },
    /**
     * 保存當前值及排序
     */
    async saveCurrentData() {
      const val = {};
      this.list.forEach((item, index) => {
        item.id = index.toString();
        val[index] = {
          Name: item.Name,
          Status: item.Status,
        };
      });
      await database.set(this.dataBase, val);
    },
    /**
     * 勾選並更新資料庫
     */
    handleChangeCheck(row) {
      const { Name, Status, id } = row;
      const ref = database.child(this.dataBase, id);
      const values = { Name, Status };
      database.update(ref, values);
    },
    /**
     * 編輯清單
     * @param {object} item - 編輯對象
     */
    handleEdit(item) {
      item.action = false;
      item.edit = true;
      item.editName = item.Name;
      setTimeout(() => {
        const dom = document.getElementById(`input${item.id}`);
        if (dom) {
          dom.focus();
        }
      }, 200);
    },
    /**
     * 儲存清單修改 / 儲存新項目
     */
    async handleSaveChange(item) {
      // 儲存修改
      if (item.id) {
        try {
          item.loading = true;
          const { editName, id, Status } = item;
          const ref = database.child(this.dataBase, id);
          const values = {
            Name: editName,
            Status,
          };
          await database.update(ref, values);
        } finally {
          item.loading = false;
        }
      }
      // 新項目
      else {
        const ref = this.dataBase;
        const values = {
          Name: item.editName,
          Status: item.Status,
        };
        await database.push(ref, values);
      }
      this.getData(); // 重整列表
    },
    /**
     * 取消編輯清單
     */
    handleCancelChange(item, index) {
      if (item.id) {
        item.edit = false;
        item.editName = "";
      } else {
        this.list.splice(index, 1);
      }
    },
    /**
     * 移除項目
     */
    async handleRemove(item) {
      const check = confirm(`刪除動作無法還原，確定刪除「${item.Name}」嗎？`);
      if (check) {
        try {
          item.loading = true;
          const { id } = item;
          const ref = database.child(this.dataBase, id);
          await database.remove(ref);
          this.getData(); // 重整列表
        } finally {
          item.loading = false;
        }
      }
    },
    /**
     * 建立新項目
     */
    handleAdd() {
      this.list.push({
        Name: "",
        edit: true,
        editName: "",
        loading: false,
        Status: false,
      });
      setTimeout(() => {
        const dom = document.getElementById("inputNew");
        if (dom) {
          dom.focus();
        }
      }, 200);
    },
    /**
     * 拖曳開始
     */
    handleDragstart(event) {
      event.dataTransfer.setData("text", event.target.dataset.index);
    },
    /**
     * 拖曳結束
     * 結束的位置加上class，可控制CSS外觀
     */
    handleDragover(event) {
      event.preventDefault();
      event.target.parentNode.classList.add("on-drag-over");
    },
    /**
     * 拖曳離開
     */
    handleDragleave(event) {
      event.target.parentNode.classList.remove("on-drag-over");
    },
    /**
     * 修改排序
     * @param {Array} dropList - 要拖曳的列表
     */
    handleDrop(event, dropList) {
      event.preventDefault();
      event.target.parentNode.classList.remove("on-drag-over");
      const index = event.dataTransfer.getData("text");
      const targetIndex = event.target.dataset.index;
      const item = dropList[index];
      dropList.splice(index, 1);
      dropList.splice(targetIndex, 0, item);
      this.saveCurrentData();
    },
    /**
     * 開啟操作選單
     */
    handleOpenAction(item) {
      this.list.forEach((ele) => {
        if (ele.id === item.id) {
          ele.action = !item.action;
        } else {
          ele.action = false;
        }
      });
    },
    /**
     * 關閉所有操作選單
     */
    handleCancelAction() {
      this.list.forEach((item) => (item.action = false));
    },
  },
};
</script>
