<template>
  <div id="app">
    <TodoList
      :is-login="isLogin"
      :login-check="loginCheck"
      :color-type="colorType"
      :user-info="userInfo"
      @hide-detail="showDetail = false"
      @login="signIn"
    />
    <div id="fix-tool">
      <ul class="color-change">
        <li
          v-for="color in colorTypes"
          :key="color.text"
          :class="color.class"
          @click="colorType = color.class"
        ></li>
      </ul>
      <div v-if="isLogin" class="user-info">
        <div
          class="img-small"
          title="Show user info"
          @click="showDetail = !showDetail"
        >
          <img :src="userInfo.photoURL" alt="" />
        </div>
        <div v-if="showDetail" class="info-detail">
          <div class="img-box"><img :src="userInfo.photoURL" alt="" /></div>
          <h3>{{ userInfo.displayName }}</h3>
          <p>{{ userInfo.email }}</p>
          <button class="change-color" :class="colorType" @click="signOut">
            <fa-icon icon="right-from-bracket" class="fix"></fa-icon>
            Sign out
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// function
import { login, logout } from "@/api/config";
import { getAuth, onAuthStateChanged } from "firebase/auth";
// component
import TodoList from "@/components/TodoList";

export default {
  name: "App",
  components: {
    TodoList,
  },
  data() {
    return {
      colorType: "default", // 顯示顏色
      userInfo: {}, // 使用者資訊
      showDetail: false, // 顯示使用者資訊
      loginCheck: false, // 已通過登入驗證
    };
  },
  beforeCreate() {
    this.colorTypes = [
      { text: "DEFAULT", class: "default" },
      { text: "GREEN", class: "green" },
      { text: "BLUE", class: "blue" },
      { text: "PURPLE", class: "purple" },
      { text: "PINK", class: "pink" },
      { text: "RED", class: "red" },
    ];
  },
  computed: {
    // 已登入
    isLogin() {
      return !!this.userInfo.uid;
    },
  },
  created() {
    this.createLoginCheck();
  },
  methods: {
    // 登入驗證
    createLoginCheck() {
      const auth = getAuth();
      onAuthStateChanged(auth, (user) => {
        this.userInfo = user || {};
        this.loginCheck = true;
      });
    },
    // 登入
    signIn() {
      login();
    },
    // 登出
    signOut() {
      logout();
    },
  },
};
</script>
